import React, { Component } from 'react';
import Table from 'react-bootstrap/Table';

class Playground extends Component {

    //default
    constructor(props) {
        super(props);
        this.state = {
            categoryListData:[]
        };
    }

    componentDidMount() {
        document.title = "Playground page";
    }

    render() {
        return (
            <div>
                <p>Hello from Playground !!!</p>
                    <Table striped bordered hover color='success' style={{'width':'100%'}}>
                        <thead>
                            <tr>
                            <th>#</th>
                            <th>name</th>
                            <th>unitOfMeasurement</th>
                            <th>metadata</th>
                            
                            <th>createdAt</th>
                            <th>updatedAt</th>

                            <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.categoryListData.map(
                                    category => 
                                    <tr key={"category_item_id_" + category.id}>
                                        <td>{category.id}</td>
                                        <td>{category.name}</td>
                                        <td>{category.unitOfMeasurement}</td>
                                        <td>{JSON.stringify(category.metadata)}</td>
                                        <td>
                                            UPDATE
                                        </td>
                                    </tr>
                                )
                            }
                        </tbody>
                    </Table>
            </div>
        )
    }

}

export default Playground;